  import Loading from '../../public/Loading'
	import Head from "../../public/head.vue"
  import Person from "../../public/person.vue"
  import Bounced from '../../public/bounced.vue'
  import test from '../../../static/js/language'
  import None from '../../public/noNumber'
  import LoadingImg from '../../public/loadingImg'
  import sex from '../../../static/js/sex'
	export default {
	  name:'userdata',
		data() {
			return {
			  api: this.GLOBAL.baseURL,
        data:{
			    token: localStorage.getItem('token')
        },
        addFriend:false,
        dataForm:'',
        textarea:'',
        userData:false,
        isInform:false,
        changeReason:true,
        informReason:{
          text:''
        },
				imgUrl:'',
        userId:'',
				activeNames:['1','2'],
        experienceList:[],
        jionList:[],
        storyList:[],
        wishList:[],
        visitorList:[],
        plannerList:[],
        isFriend:false,
        statu:'',
        informList:[],
        zanList:[],
        isZan:false,
        owrId:'',
        leixing:'',
        who:'',
        isLoading:false,
        AllLoading:false,
        total:0,
        activeName:'first',
        commonetList:[],
        playCommenList:[],
        editSearch:'搜索',
        content:'',
        isProblem:false,
        radio:'',
        retrnText:'',
        dialogImageUrl: '',
        dialogVisible: false,
        image:[],
        langusgeList:[],
        other_laguage:[],
        sexA:[],
        pageIndex:1,
        addMore:false,
        comAll:0,
        playTotal:0,
        commentIndex:1,
        playIndex:1
			};
		},
		components:{
			Head,
      Person,
      Bounced,
      None,
      LoadingImg,
      Loading
		},
    created:function(){
	    window.scroll(0,0)
      this.getParams()
      this.langusgeList = test()
      this.sexA = sex()
    },
    watch:{
      '$route':'getParams',
      userId: function(){
        if(this.userId){
          this.checkUser()
          this.commentIndex = 1
            this.playIndex = 1
          this.getComment(1,this.userId)
          this.getPlaycomment(1,this.userId)

        }else{
          this.getOwer()
          this.who = false
          this.commentIndex = 1
          this.playIndex = 1
        }
        window.scroll(0,0)
      }
    },
		methods:{
      lookMore(){
        this.getJionActive(this.index)
      },
      comentChange(val){
        if(this.userId){
          this.getComment(val,this.userId)
        }else{
          this.getComment(val,this.owrId)
        }
      },
      playChange(val){
          if(this.userId){
            this.getPlaycomment(val,this.userId)
          }else{
            this.getPlaycomment(val,this.owrId)
          }
      },
      toReturn(){
        this.isProblem = !this.isProblem
      },
      toSend(){
        this.$http.post(this.api + '/FeedBackS',{
          token: localStorage.getItem('token'),
          content: this.retrnText,
          type: this.radio,
          image: JSON.stringify(this.image)
        })
          .then(res=>{
            if(res.data.code == 1){
              this.isProblem = false
              this.$message({
                type: 'success',
                message: '提交成功!'
              })
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.toSend()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      uploadSucess(file){
        this.image.push({image_id: file.data.image_id})
      },
      handleRemove(file, fileList) {

      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      toPlayer(){
        this.$router.push( '/authentication')
      },
      toPerson(item,index){
        /*this.userId = item.user_id*/
        this.$router.push({
          path: '/userdata',
          query: {
            information: item.user_id
          }
        })

      },
	    //申请志愿者
      sendVolun(){
        this.$router.push({
          path: '/Validation',
          query:{
            information: 'volunteer'
          }
        })
      },
	    //保存介绍
      sendContent(msg){
        this.$http.post(this.api + '/home/User/saveuser',{
          token: localStorage.getItem('token'),
          introduce: msg
        })
          .then(res=>{
            if(res.data.code == 1){
              this.userData = false
              this.$message({
                type: 'success',
                message: '编辑成功!'
              })
              this.getOwer()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.sendContent()
                })
            }else{

            }
          })
      },
      searchHome(){
        this.$router.push({
          name: 'homeSearch',
          query:{
            key: this.content,
            type:0
          }
        })
      },
      getVal(msg){
        this.content = msg
      },
	    getPlaycomment(val,id){
	      this.$http.post(this.api + '/CommentPlanner',{
	        token: localStorage.getItem('token'),
          page:val,
          user_id:id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.playCommenList = res.data.data.data
              this.playTotal = res.data.data.total
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getPlaycomment(val,id)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      handleClick(val){
        this.commentIndex = 1
        this.playIndex = 1
	      if(val = 'second'){
	        if(this.userId){
            this.getPlaycomment(1,this.userId)
          }else{
            this.getPlaycomment(1,this.owrId)
          }
        }else{
          if(this.userId){
            this.getComment(1,this.userId)
          }else{
            this.getComment(1,this.owrId)
          }
        }
      },
	    toList(item,index){
	      this.$router.push({
          path:'/wishPage',
          query:{
            group_id: item.group_id,
            name: item.group_name
          }
        })
      },
			handleChange(){
				
			},
      show(){
			  this.$alert("hahhaha")
      },
      toAlter(item,index){
        this.$router.push({
          path:'/publishPage',
          query:{
            information: item.activity_id
          }
        })
      },
      toStory(item,index){
        this.$router.push({
          path: '/storyPage',
          query:{
            information: item.story_id
          }
        })
      },
      addFridends() {
			  this.$http.post(this.api + '/home/Friend/ask_friend',{
			    token: localStorage.getItem('token'),
          f_user_id: this.userId,
          msg: this.textarea
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                message: '消息已发送成功',
                type: 'success'
              });
              this.addFriend = false;
              this.textarea = ''
            }else if(res.data.code == 3){
              alert(res.data.msg)
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
          .catch(error=>{
            alert(error)
          })
      },
      closeUserdata(){
			  this.userData = false
      },
      changeForm(){
        this.changeReason = false
      },
      informAblish(){
        this.changeReason = true
      },
      getParams(){
        this.userId = this.$route.query.information
      },
      getOwer(){
        this.$http.post(this.api + '/home/User/get_user',{
          token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              this.statu = 0
              let data =  res.data.data[0]
              this.dataForm = res.data.data[0]
              if(data.head_image){
                this.imgUrl = data.headimage.domain + data.headimage.themb_url
              }else{
                this.imgUrl = '../../static/img/static/user.png'
              }
              if(data.other_language){
                var a = []
                for(let m = 0;m<data.other_language.split(',').length;m++){
                  a.push(parseInt(data.other_language.split(',')[m]))
                }
                this.other_laguage = a
              }
              this.owrId = data.user_id
              this.getOwerActive()
              this.getJionActive(this.pageIndex)
              this.getStory()
              this.getCollect()
              this.getComment(1,this.owrId)
              this.getPlaycomment(1,this.owrId)
            }else if(res.data.code == 3){
              this.$router.push('/')
              localStorage.removeItem('isLogin')
              localStorage.removeItem('userImg')
            }else{
              this.$router.push('/')
              localStorage.removeItem('isLogin')
              localStorage.removeItem('userImg')
            }
          })
      },
      getOther(){
        this.$http.post(this.api + '/home/User/get_otheruser',{
          token: localStorage.getItem('token'),
          user_id: this.userId
        })
          .then(res=>{
            if(res.data.code == 1){
              this.statu = 1
              let data =  res.data.data
              this.dataForm = res.data.data
              if(data.headimage){
                this.imgUrl = data.headimage.domain + data.headimage.themb_url
              }else{
                this.imgUrl = '../../static/img/static/user.png'
              }
                this.wishList = data.collect_group
                this.storyList = data.story_list
                this.isFriend = data.is_friend? false: true
                this.experienceList = data.create_activity
            }else if(res.data.code == 3){
                this.$router.push('/')
                localStorage.removeItem('isLogin')
                localStorage.removeItem('userImg')
              }else{
                this.$router.push('/')
                localStorage.removeItem('isLogin')
                localStorage.removeItem('userImg')
              }
          })
      },
      getOwerActive(){
			  this.$http.post(this.api + '/home/Activity/complete',{
			    token: localStorage.getItem('token'),
          flag:3
        })
          .then(res=>{
            if(res.data.code == 1){
              this.experienceList = res.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getOwerActive()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getJionActive(val){
			  this.$http.post(this.api + '/vipact',{
			    token: localStorage.getItem('token'),
          iscomplete: 1,
          page:val
        })
          .then(res=>{
            if(res.data.code == 1){
              if(res.data.data.data.length){
                this.addMore = true
                this.jionList.push(res.data.data.data)
                this.index ++
              }else{
                this.addMore = false
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getJionActive(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getStory(){
			  this.$http.post(this.api + '/storyc',{
			    token: localStorage.getItem('token'),
          sort: 1,
          page:1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.storyList = res.data.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getStory()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getCollect(){
			  this.$http.post(this.api + '/home/Comment/collegroup_list',{
			    token: localStorage.getItem('token'),
        })
          .then(res=>{
            if(res.data.code == 1){
              this.wishList = res.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getCollect()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      checkUser(){
        this.AllLoading = true
			  if(localStorage.getItem('isLogin')){
          this.$http.post(this.api + '/home/User/get_user',{
            token: localStorage.getItem('token')
          })
            .then(res=>{
              if(res.data.code == 1){
                this.owrId = res.data.data[0].user_id
                if(res.data.data[0].user_id == this.userId){
                  this.getOwer()
                  this.who = false
                }else{
                  this.getOther()
                  this.who = true
                }

              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.checkUser()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }else{
          this.getOther()
        }

      },
      getComment(val,id){
			  this.$http.post(this.api + '/CommentVisit',{
			    token: localStorage.getItem('token'),
          page:val,
          user_id:id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.commonetList = res.data.data.data
              this.comAll = res.data.data.total
              this.AllLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=> {
                  localStorage.setItem('token',res.data.data)
                  this.getComment(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      //举报用户
      openInform(index,item){
        this.isInform = true
        this.getInform()
      },
      getInform(){
        this.$http.post(this.api + '/home/Activity/question',{
          token: localStorage.getItem('token'),
          flag: 6
        })
          .then(res=>{
            if(res.data.code == 1){
              this.informList = []
              for(let i = 0;i<res.data.data[0].option.length;i++){
                let a = res.data.data[0].option[i].name.split('/')
                this.informList.push({name: a[0],content:a[1],option_id:res.data.data[0].option[i].option_id})
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getInform()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      closeInform(item){
        if(item){
          this.$http.post(this.api + '/ReportU',{
            token: localStorage.getItem('token'),
            flag: 3,
            table_id: this.storyId,
            option_id: item.option_id
          })
            .then(res=>{
              if(res.data.code == 1){
                this.isInform = false
                this.$message({
                  type:'success',
                  message:'提交成功'
                })
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.closeInform(item)
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }else{
          this.isInform = false
        }

      },
      informSend(){
        if(this.informReason.text.trim().length <= 0){
          this.$message({
            type:'error',
            message: '请填写您的理由'
          })
        }else{
          this.$http.post(this.api + '/ReportU',{
            token: localStorage.getItem('token'),
            flag: 6,
            table_id: this.storyId,
            content: this.informReason.text
          })
            .then(res=>{
              if(res.data.code == 1){
                this.isInform = false
                this.informReason.text = ''
                this.changeReason = true
                this.$message({
                  type:'success',
                  message:'提交成功'
                })
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.informSend()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }

      },
      closeZan(){
			  this.isZan = false
        this.zanList = []
      },
      getMyPraise(val,num){
        this.$http.post(this.api + '/MyPraise',{
          token: localStorage.getItem('token'),
          user_id: val,
          page:num,
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.zanList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getMyPraise(val,num)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getAttentionL(val,num){
        this.$http.post(this.api + '/AttentionL',{
          token: localStorage.getItem('token'),
          user_id: val,
          page:num
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.zanList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getAttentionL(val,num)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      lookZan(val){
			  this.isLoading = true
        this.isZan = true
			  if(this.userId){
          if(val == 1){
            this.leixing = 'zan'
            this.getMyPraise(this.userId,1)
          }else if(val == 2){
            this.leixing = 'fen'
            this.getAttentionL(this.userId,1)
          }else{
            this.leixing = 'guan'
            this.getGuanzhu(this.userId,1)
          }
        }else{
          if(val == 1){
            this.leixing = 'zan'
            this.getMyPraise(this.owrId,1)
          }else if(val == 2){
            this.leixing = 'fen'
            this.getAttentionL(this.owrId,1)
          }else{
            this.leixing = 'guan'
            this.getGuanzhu(this.owrId,1)
          }
        }
      },
      getGuanzhu(user_id,val){
        this.$http.post(this.api + '/AttentionOL',{
          token: localStorage.getItem('token'),
          user_id: user_id,
          page: val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.zanList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.getGuanzhu(user_id,val)
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      handleCurrentChange(val){
        this.isLoading = true
        this.zanList = []
			  if(this.userId){
          if(this.leixing == 'zan'){
            this.getMyPraise(this.userId,val)
          }else if(this.leixing == 'fen'){
            this.getAttentionL(this.userId,val)
          }else{
            this.getGuanzhu(this.userId,val)
          }
        }else{
          if(this.leixing == 'zan'){
            this.getMyPraise(this.owrId,val)
          }else if(this.leixing == 'fen'){
            this.getAttentionL(this.owrId,val)
          }else{
            this.getGuanzhu(this.owrId,val)
          }
        }
      },
      toCheckUser(val){
        this.isZan = false
        this.$router.push({
          path: '/userdata',
          query: {
            information: val.user_id
          }
        })
      },
		},
    mounted(){

    },
	}



